.creator-dashboard-container {
  padding: 40px 20px;
  max-width: 1400px;
  margin: 0 auto;
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(to bottom right, #f5f9ff, #e3f2fd);
  min-height: 100vh;
}

.creator-dashboard-title {
  font-size: 2.8rem;
  font-weight: 700;
  color: #003366;
  margin-bottom: 30px;
}

.creator-new-campaign-btn {
  display: inline-block;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  background-color: #007bff;
  color: #fff;
  border-radius: 10px;
  margin-bottom: 30px;
  transition: background-color 0.3s ease;
  text-decoration: none;
}

.creator-new-campaign-btn:hover {
  background-color: #0056b3;
}

.creator-no-campaigns {
  font-size: 1.2rem;
  color: #444;
  margin-top: 30px;
}

.creator-campaign-list {
  display: flex;
  flex-wrap: wrap;
  gap: 35px;
  justify-content: flex-start;
  align-items: stretch;
}

.creator-campaign-card {
  flex: 1 1 300px;
  min-width: 425px;
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
  padding: 20px;
  display: flex;
  flex-direction: column;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  min-height: 460px;
}

.creator-campaign-card:hover {
  transform: translateY(-5px);
}

.creator-campaign-card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 12px;
}

.creator-campaign-card h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1e3a8a;
  margin-bottom: 6px;
  line-height: 1.3;
  height: 2.5em;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.creator-campaign-card p {
  font-size: 0.9rem;
  color: #333;
  margin: 4px 0;
  line-height: 1.3;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.creator-campaign-card strong {
  color: #1e40af;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.modal-content {
  background: #ffffff;
  padding: 30px;
  border-radius: 10px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 14px;
  font-size: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
}

/* Progress bar */
.progress-container {
  width: 100%;
  height: 10px;
  background-color: #ddd;
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 0;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(to right, #00c6ff, #0072ff);
  border-radius: 5px;
  transition: width 0.4s ease-in-out;
}

/* Action buttons */
.creator-button-group {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

.creator-button-group button {
  padding: 8px 14px;
  font-size: 0.9rem;
  font-weight: 500;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  color: white;
}

.creator-button-edit {
  background-color: #007bff;
}

.creator-button-edit:hover {
  background-color: #0062cc;
}

.creator-button-delete {
  background-color: #dc3545;
}

.creator-button-delete:hover {
  background-color: #b52a38;
}

.creator-button-view {
  background-color: #28a745;
}

.creator-button-view:hover {
  background-color: #218838;
}


/* Modal overlay background */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Modal box */
.modal-content {
  background: #ffffff;
  padding: 30px;
  border-radius: 10px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
  animation: slideFade 0.3s ease;
}

/* Close button */
.close-btn {
  position: absolute;
  top: 10px;
  right: 14px;
  font-size: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  color: #555;
}

/* Investor list */
.modal-content h3 {
  margin-bottom: 15px;
  color: #003366;
  font-size: 1.4rem;
}

.modal-content ul {
  padding-left: 20px;
  margin-top: 10px;
}

.modal-content li {
  font-size: 1rem;
  padding: 6px 0;
  border-bottom: 1px solid #eee;
  color: #333;
}

.modal-content li strong {
  color: #1e3a8a;
}

/* Subtle fade-in animation */
@keyframes slideFade {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* âœ… Responsive: full width on small screens */
@media (max-width: 768px) {
  .campaign-list {
    grid-template-columns: 1fr;
  }
}

.creator-dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.logout-button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 8px 16px;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.logout-button:hover {
  background-color: #e60000;
}
