/* UsersPage.css */

.admin-content-container {
    padding: 30px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .admin-content-container h2 {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
    color: #2c3e50;
  }
  
  .admin-table {
    width: 100%;
    border-collapse: collapse;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.05);
    background-color: #fff;
  }
  
  .admin-table thead {
    background-color: #f5f7fa;
  }
  
  .admin-table th,
  .admin-table td {
    padding: 14px 20px;
    text-align: left;
    font-size: 15px;
    border-bottom: 1px solid #ecf0f1;
  }
  
  .admin-table th {
    font-weight: 600;
    color: #34495e;
  }
  
  .admin-table tbody tr:hover {
    background-color: #f0f8ff;
    transition: background 0.2s ease-in-out;
  }
  
  .admin-table td {
    color: #2d3436;
  }
  
  .role-badge {
    padding: 4px 10px;
    border-radius: 20px;
    font-size: 13px;
    font-weight: 500;
    text-transform: capitalize;
    color: white;
  }
  
  .role-badge.admin {
    background-color: #2980b9;
  }
  
  .role-badge.creator {
    background-color: #27ae60;
  }
  
  .role-badge.investor {
    background-color: #8e44ad;
  }
  