.etl-container {
    padding: 2rem;
    background: #f7f9fc;
    min-height: 100vh;
  }
  
  .etl-heading {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 2rem;
    color: #333;
  }
  
  .etl-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
  }
  
  .etl-card {
    flex: 1 1 450px;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(6px);
    border-radius: 16px;
    padding: 2rem;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.06);
    display: flex;
    flex-direction: column;
    gap: 1rem;
    transition: transform 0.3s ease;
  }
  
  .etl-card:hover {
    transform: translateY(-4px);
  }
  
  .etl-card h3 {
    font-size: 1.3rem;
    margin-bottom: 1rem;
    color: #222;
  }
  
  .etl-card button {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 12px 16px;
    font-weight: 500;
    font-size: 0.95rem;
    border: none;
    border-radius: 10px;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.25s ease;
  }
  
  .etl-card button:hover {
    background-color: #0056b3;
  }
  
  .etl-card button.danger {
    background-color: #dc3545;
  }
  
  .etl-card button.danger:hover {
    background-color: #a91c2f;
  }
  
  .preview-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.65);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
  }
  
  .modal-content {
    background: #fff;
    padding: 1.5rem;
    border-radius: 10px;
    width: 90%;
    max-width: 800px;
    max-height: 80vh;
    overflow-y: auto;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  }
  
  .modal-content pre {
    background: #f0f0f0;
    padding: 1rem;
    font-size: 0.85rem;
    white-space: pre-wrap;
    word-wrap: break-word;
    overflow-x: auto;
    border-radius: 5px;
  }
  
  .modal-content button {
    margin-top: 1rem;
    background: #007bff;
    border: none;
    padding: 8px 14px;
    color: white;
    border-radius: 5px;
    cursor: pointer;
  }
  