.activity-log-container {
    padding: 40px 20px;
    background: linear-gradient(to bottom right, #f7fafd, #eaf4ff);
    font-family: 'Segoe UI', sans-serif;
    min-height: 100vh;
    max-width: 1400px;
    margin: 0 auto;
  }
  
  .activity-log-title {
    font-size: 2rem;
    font-weight: 700;
    color: #003366;
    margin-bottom: 30px;
  }
  
  .activity-log-table {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
    background: #fff;
  }
  
  .activity-log-header, .activity-log-row {
    display: grid;
    grid-template-columns: 60px 140px 220px 1fr 180px;
    padding: 14px 20px;
    border-bottom: 1px solid #e0e0e0;
    font-size: 0.95rem;
    align-items: center;
  }
  
  .activity-log-header {
    background-color: #003366;
    color: white;
    font-weight: 600;
  }
  
  .activity-log-row:nth-child(even) {
    background-color: #f9fbfd;
  }
  
  .log-badge {
    display: inline-block;
    padding: 4px 10px;
    border-radius: 14px;
    font-weight: 500;
    font-size: 0.85rem;
    text-transform: capitalize;
    width: fit-content;
  }
  
  .log-badge.create {
    background: #e0f9ec;
    color: #228b22;
  }
  
  .log-badge.update {
    background: #fff3cd;
    color: #856404;
  }
  
  .log-badge.delete {
    background: #f8d7da;
    color: #721c24;
  }
  
  .log-badge.info {
    background: #d1ecf1;
    color: #0c5460;
  }
  
  .no-logs {
    text-align: center;
    padding: 20px;
    color: #555;
    font-style: italic;
  }
  