.admin-dashboard-container {
    padding: 2rem;
    font-family: 'Segoe UI', sans-serif;
    background-color: #f5f7fa;
    min-height: 100vh;
  }
  
  .admin-dashboard-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 2rem;
    color: #333;
  }
  
  /* Top Stat Cards */
  .dashboard-cards,
  .mini-cards-row,
  .chart-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 1.5rem;
    margin-bottom: 2.5rem;
  }
  
  .card,
  .mini-card,
  .chart-box {
    background: white;
    padding: 1.5rem;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
    text-align: center;
    min-height: 130px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 0.2s ease-in-out;
  }
  
  .card:hover,
  .mini-card:hover {
    transform: translateY(-4px);
  }
  
  .card h3,
  .mini-card h4 {
    font-size: 1rem;
    color: #444;
    margin-bottom: 0.5rem;
  }
  
  .card p,
  .mini-card p {
    font-size: 1.5rem;
    font-weight: bold;
    color: #007bff;
  }
  
  /* Chart containers */
  .chart-box h4 {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 1rem;
    text-align: left;
  }
  