/* src/admin/styles/Topbar.css */

.topbar {
    height: 60px;
    background-color: #f4f4f4;
    padding: 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
  .topbar-logo {
    font-size: 20px;
    font-weight: bold;
    color: #2a2a3d;
  }
  
  .topbar-right {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .topbar-user {
    font-weight: 500;
    color: #333;
  }
  
  .topbar-role {
    background-color: #0077cc;
    color: white;
    padding: 4px 10px;
    border-radius: 12px;
    font-size: 12px;
    text-transform: capitalize;
  }
  
  .logout-button {
    margin-left: 1rem;
    padding: 6px 12px;
    font-size: 14px;
    font-weight: 500;
    background-color: #e53935;
    color: #fff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .logout-button:hover {
    background-color: #c62828;
  }
  