body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
}
.home-page, .login-page {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: white;
}
.login-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
input, button {
    padding: 10px;
    font-size: 16px;
}
button {
    cursor: pointer;
    background-color: #007bff;
    color: white;
    border: none;
}