.admin-investments-container {
    padding: 40px;
    background: linear-gradient(to bottom right, #f5faff, #e4f1fd);
    min-height: 100vh;
    font-family: 'Segoe UI', sans-serif;
  }
  
  .admin-investments-heading {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 20px;
    color: #09365f;
  }
  
  .investment-filter-bar {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
    font-weight: 500;
  }
  
  .filter-dropdown {
    padding: 8px 12px;
    border-radius: 6px;
    border: 1px solid #ccc;
    font-size: 0.95rem;
    background-color: white;
    font-weight: 500;
    transition: border-color 0.3s ease;
  }
  
  .filter-dropdown:focus {
    border-color: #0056b3;
    outline: none;
  }
  
  .investment-table-wrapper {
    overflow-x: auto;
    background: white;
    border-radius: 10px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.05);
  }
  
  .investment-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .investment-table th {
    background-color: #002b5c;
    color: white;
    padding: 12px;
    font-weight: 600;
    text-align: left;
  }
  
  .investment-table td {
    padding: 14px 12px;
    font-size: 0.95rem;
    color: #333;
    border-bottom: 1px solid #f0f0f0;
  }
  
  .investment-table tr:hover {
    background-color: #f7faff;
    transition: background 0.2s ease;
  }
  