/* Full page background */
.login-page {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('../assets/c3.jpg'); /* Replace with your actual image path */
    background-size: cover;
    background-size: cover;  /* Ensures image covers the whole screen */
    background-position: center; /* Centers the image */
    background-repeat: no-repeat; /* Prevents the repeat issue */
    background-attachment: fixed;
    background-position: center;
    font-family: 'Poppins', sans-serif;
    
}

/* Card container */
.login-card {
    background: rgba(255, 255, 255, 0.95);
    padding: 40px;
    border-radius: 16px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
    width: 420px;
    text-align: center;
    backdrop-filter: blur(10px);
}

/* Title */
.login-card h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
}

/* Subtitle */
.subtext {
    font-size: 14px;
    color: #555;
    margin-bottom: 20px;
}

/* Form fields */
.login-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* Input & Select */
.login-input, .login-select {
    padding: 12px;
    font-size: 14px;
    border: 1px solid #ddd;
    border-radius: 8px;
    outline: none;
}

/* Login Button */
.login-btn {
    background-color: #424344;
    color: white;
    padding: 12px;
    border: none;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-btn:hover {
    background-color: #0056b3;
}

/* Error message */
.error-message {
    color: #d9534f;
    margin-top: 10px;
    font-size: 14px;
}

/* Signup link */
.signup-link {
    margin-top: 15px;
    font-size: 14px;
    color: #555;
}

.signup-button {
    color: #007bff;
    font-weight: bold;
    cursor: pointer;
    text-decoration: underline;
}

.signup-button:hover {
    color: #0056b3;
}

/* Mobile responsive */
@media (max-width: 480px) {
    .login-card {
        width: 90%;
        padding: 20px;
    }

    .login-card h2 {
        font-size: 20px;
    }
}
