/* src/admin/styles/AdminLayout.css */

.admin-layout {
  display: flex;
  height: 100vh;
  width: 100%;
  background-color: #f9f9f9;
  overflow: hidden;
}

.admin-main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.admin-page-content {
  padding: 30px 40px;
  background-color: #ffffff;
  min-height: calc(100vh - 60px); 
}

