/* Background & Base Layout */
.homepage-container {
    font-family: 'Segoe UI', sans-serif;
    color: #333;
    margin: 0;
    padding: 0;
}

.hero-section {
    position: relative;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.hero-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
    filter: brightness(0.85);
}

.hero-section .overlay {
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 2rem;
    background-color: rgba(0, 0, 0, 0.9);
    backdrop-filter: blur(6px);  
    border-radius: 12px;
    max-width: 700px;
    margin: auto;
}

.hero-section::before {
    display: none;
  }  

.overlay {
    position: relative;
    z-index: 2;
    background-color: rgb(0, 0, 0); 
    padding: 50px 60px;
    border-radius: 20px;
    color: #ffffff;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8);
    border: 2.5px solid #1A1A1A;
    max-width: 800px;
    text-align: center;
}


.overlay h1 {
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 20px;
}

.overlay p {
    font-size: 1.2rem;
    font-weight: 400;
    color: #ffffff;
}


.cta-buttons .btn {
    background-color: #0066ff;
    color: white;
    padding: 12px 25px;
    margin: 0 10px;
    text-decoration: none;
    border-radius: 8px;
    transition: 0.3s ease;
}

.cta-buttons .btn:hover {
    background-color: #0052cc;
}

.btn.secondary {
    background-color: #ffffff;
    color: #0066ff;
    border: 1px solid #0066ff;
}

.btn.secondary:hover {
    background-color: #f0f0f0;
}

/* ========== HOW IT WORKS ========== */
.how-it-works {
    padding: 80px 20px;
    background: linear-gradient(to right, #f8fdff, #ecf5f9);
    text-align: center;
}

.how-it-works h2 {
    font-size: 2.8rem;
    margin-bottom: 50px;
    color: #003049;
}

.steps {
    display: flex;
    justify-content: center;
    gap: 40px;
    flex-wrap: wrap;
}

.step {
    background: #ffffff;
    border-radius: 16px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
    padding: 30px 25px;
    max-width: 280px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: default;
}

.step:hover {
    transform: translateY(-8px);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.12);
}

.step img {
    width: 60px;
    margin-bottom: 20px;
}

.step h3 {
    font-size: 1.4rem;
    margin-bottom: 10px;
    color: #004c6d;
}

.step p {
    font-size: 1rem;
    color: #444;
}

.why-us {
    padding: 80px 40px;
    background: linear-gradient(to bottom, #ffffff, #f4f6f8);
    text-align: center;
}

.why-us h2 {
    font-size: 2.8rem;
    margin-bottom: 50px;
    color: #1a1a1a;
}

.reasons {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 40px;
    max-width: 1200px;
    margin: 0 auto;
}

.reason {
    background-color: #ffffff;
    border-radius: 12px;
    padding: 30px 20px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.reason:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

.reason img {
    width: 60px;
    height: auto;
    margin-bottom: 20px;
}

.reason h4 {
    font-size: 1.4rem;
    margin-bottom: 12px;
    color: #004d40;
}

.reason p {
    font-size: 1rem;
    color: #555;
}

/* Footer */
.footer {
    background-color: #7f8e9d;
    color: white;
    text-align: center;
    padding: 30px 20px;
}

.footer-links {
    margin-top: 10px;
}

.footer-links a {
    color: white;
    margin: 0 12px;
    text-decoration: none;
    font-size: 0.95rem;
}

.footer-links a:hover {
    text-decoration: underline;
}
