/* src/admin/styles/Sidebar.css */

.sidebar {
  width: 240px;
  height: 100vh;
  background-color: #1e1e2f;
  color: white;
  padding: 20px;
  box-shadow: 2px 0 6px rgba(0, 0, 0, 0.2);
}

.sidebar-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
}

.sidebar-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-links li {
  margin-bottom: 18px;
}

.sidebar-links li a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #c7c7d9;
  font-size: 16px;
  padding: 10px;
  border-radius: 6px;
  transition: background 0.2s ease;
}

.sidebar-links li a:hover,
.sidebar-links li a.active {
  background-color: #2a2a3d;
  color: #ffffff;
}

.sidebar-links li a svg {
  margin-right: 10px;
  font-size: 18px;
}
