.admin-campaigns {
    padding: 30px 20px;
    width: 100%;
    box-sizing: border-box;
    background: #f8f9fa;
    min-height: 100vh;
  }
  
  .admin-campaigns h2 {
    margin-bottom: 24px;
    font-size: 28px;
    font-weight: 700;
    color: #1e1e1e;
  }
  
  .campaign-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 32px;
  }
  
  .campaign-card {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.05);
    overflow: hidden;
    transition: transform 0.2s ease;
    width: calc(33.333% - 24px);
    display: flex;
    flex-direction: column;
    min-width: 320px;
    max-width: 420px;
  }
  
  .campaign-card:hover {
    transform: translateY(-4px);
  }
  
  .campaign-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .campaign-info {
    padding: 16px 20px;
    font-size: 14px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .campaign-info h3 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #111827;
    line-height: 1.4;
  }
  
  .campaign-info p {
    margin: 6px 0;
    color: #555;
  }
  
  .campaign-info p strong {
    color: #1a1a1a;
  }
  
  .status {
    display: inline-block;
    margin-top: 10px;
    padding: 4px 12px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
    text-transform: capitalize;
  }
  
  .status.approved {
    background: #d1f5d3;
    color: #0a6c30;
  }
  
  .status.rejected {
    background: #ffd9d9;
    color: #b40000;
  }
  
  .status.pending {
    background: #fff0b3;
    color: #6d4c00;
  }
  
  .status.completed {
    background: #d4eaff;
    color: #0059b3;
  }
  
  .admin-actions {
    display: flex;
    justify-content: flex-start;
    margin-top: 12px;
    gap: 12px;
    flex-wrap: wrap;
  }
  
  .admin-actions button {
    padding: 6px 14px;
    font-size: 13px;
    font-weight: 500;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background 0.2s ease;
    min-width: 90px;
  }
  
  button.approve {
    background-color: #4caf50;
    color: white;
  }
  
  button.approve:hover {
    background-color: #45a045;
  }
  
  button.reject {
    background-color: #f44336;
    color: white;
  }
  
  button.reject:hover {
    background-color: #d7372c;
  }
  
  /* Responsive Grid */
  @media (max-width: 992px) {
    .campaign-card {
      width: calc(50% - 24px);
    }
  }
  
  @media (max-width: 600px) {
    .campaign-card {
      width: 100%;
    }
  }
  