.invest-page-container {
  padding: 40px;
  background-color: #f4fefc;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.invest-card {
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  padding: 30px;
  width: 100%;
  max-width: 620px;
  transition: transform 0.2s ease-in-out;
}

.invest-card:hover {
  transform: translateY(-3px);
}

.invest-card img {
  width: 100%;
  height: 260px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 20px;
}

.invest-card h2 {
  color: #1b3c2c;
  font-size: 1.6rem;
  margin-bottom: 10px;
}

.invest-card p {
  font-size: 1rem;
  color: #4d7754;
  margin-bottom: 8px;
}

.invest-card form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.invest-card input[type="number"] {
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #cdd9c3;
  border-radius: 6px;
  margin-bottom: 16px;
  outline: none;
  transition: all 0.3s ease;
}

.invest-card input[type="number"]:focus {
  border-color: #4CAF50;
  box-shadow: 0 0 5px rgba(76, 175, 80, 0.4);
}

.invest-btn {
  padding: 12px;
  background-color: #4CAF50;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.invest-btn:hover {
  background-color: #3c913f;
}

.error-message {
  color: #d32f2f;
  margin-top: 12px;
  font-weight: 500;
  text-align: center;
  background: #fdecea;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #f8c8c8;
}

.success-message {
  color: #2e7d32;
  margin-top: 12px;
  font-weight: 500;
  text-align: center;
  background: #e6f4ea;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #bde5c8;
}
