.login-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../assets/c3.jpg');
    background-size: cover;
    background-position: center;
    backdrop-filter: brightness(0.85);
    padding: 20px;
  }
  
  .login-box {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 18px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
    padding: 40px 35px;
    width: 100%;
    max-width: 420px;
    text-align: center;
    animation: slideIn 0.4s ease-in-out;
  }
  
  .login-box h2 {
    font-size: 24px;
    margin-bottom: 6px;
    color: #222;
  }
  
  .login-box p {
    font-size: 14px;
    margin-bottom: 25px;
    color: #555;
  }
  
  .login-box form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  /* Uniform form elements */
  .login-box input,
  .login-box select,
  .login-box button {
    padding: 12px;
    font-size: 15px;
    border-radius: 8px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #ccc;
  }
  
  .login-box input:focus,
  .login-box select:focus {
    border-color: #007bff;
    outline: none;
  }
  
  /* Button specific styles */
  .login-box button {
    background-color: #007bff;
    color: white;
    font-weight: 500;
    border: none;
    transition: background-color 0.25s;
  }
  
  .login-box button:hover {
    background-color: #0056b3;
  }
  
  .error-message {
    margin-top: 15px;
    color: red;
    font-size: 14px;
  }
  
  @keyframes slideIn {
    from {
      transform: translateY(-30px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  