.manage-admins-container {
    padding: 30px;
    width: 100%;
    box-sizing: border-box;
  }
  
  .manage-admins-container h2 {
    font-size: 28px;
    margin-bottom: 20px;
    color: #0b2545;
  }
  
  .add-admin-btn {
    background-color: #007bff;
    color: white;
    padding: 10px 18px;
    border: none;
    border-radius: 6px;
    margin-bottom: 20px;
    cursor: pointer;
    font-weight: 500;
    font-size: 15px;
  }
  
  .add-admin-btn:hover {
    background-color: #0056b3;
  }
  
  .admin-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 8px;
    background-color: transparent;
  }
  
  .admin-table th {
    background-color: #00264d;
    color: white;
    text-align: left;
    padding: 14px 18px;
    font-size: 14px;
  }
  
  .admin-table td {
    padding: 14px 18px;
    background-color: #ffffff;
    font-size: 15px;
    color: #333;
    vertical-align: middle;
    border-bottom: 1px solid #e0e0e0;
  }
  
  .status-badge {
    padding: 4px 12px;
    border-radius: 20px;
    font-size: 13px;
    font-weight: 500;
    display: inline-block;
    text-align: center;
  }
  
  .status-badge.active {
    background-color: #d4f8d4;
    color: #2e7d32;
  }
  
  .status-badge.disabled {
    background-color: #ffdede;
    color: #b71c1c;
  }
  
  /* Fix alignment of action buttons */
  .admin-actions {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    height: 100%;
    min-height: 48px;
    padding: 0;
    margin: 0;
  }
  
  .disable-btn,
  .enable-btn,
  .delete-btn {
    padding: 6px 12px;
    font-size: 13px;
    font-weight: 500;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    white-space: nowrap;
    transition: background-color 0.3s ease;
    height: 32px;              /* FIXED HEIGHT FOR PERFECT ALIGNMENT */
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .disable-btn {
    background-color: #ffc107;
    color: #212121;
  }
  
  .disable-btn:hover {
    background-color: #e0a800;
  }
  
  .enable-btn {
    background-color: #4caf50;
    color: white;
  }
  
  .enable-btn:hover {
    background-color: #43a047;
  }
  
  .delete-btn {
    background-color: #f44336;
    color: white;
  }
  
  .delete-btn:hover {
    background-color: #d32f2f;
  }
  
  
  /* Modal styling */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
  
  .admin-modal {
    background: #fff;
    padding: 30px 40px;
    border-radius: 14px;
    width: 400px;
    max-width: 90%;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
    animation: pop 0.3s ease;
  }
  
  @keyframes pop {
    from {
      transform: scale(0.9);
      opacity: 0;
    }
    to {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  .admin-modal h3 {
    font-size: 22px;
    margin-bottom: 20px;
    color: #1a1a1a;
  }
  
  .admin-modal input {
    width: 100%;
    padding: 10px 14px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 15px;
    transition: border 0.3s ease;
  }
  
  .admin-modal input:focus {
    outline: none;
    border-color: #007bff;
  }
  
  .modal-buttons {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    margin-top: 10px;
  }
  
  .submit-btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 6px;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .submit-btn:hover {
    background-color: #0069d9;
  }
  
  .cancel-btn {
    background-color: #e0e0e0;
    color: #333;
    border: none;
    padding: 8px 16px;
    border-radius: 6px;
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .cancel-btn:hover {
    background-color: #d0d0d0;
  }
  
  