.thank-you-container {
    min-height: 100vh;
    background-color: #f4fefc;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 30px;
    font-family: 'Segoe UI', sans-serif;
    position: relative;
    overflow: hidden;
  }
  
  .thank-you-animation {
    width: 400px;
    max-width: 90%;
    margin-bottom: 20px;
  }
  
  .thank-you-container h1 {
    font-size: 2rem;
    font-weight: 600;
    color: #1b3c2c;
    margin-bottom: 10px;
  }
  
  .thank-you-container p {
    font-size: 1.1rem;
    color: #4d7754;
    margin-bottom: 20px;
  }
  
  .thank-you-container button {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #4CAF50;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .thank-you-container button:hover {
    background-color: #388e3c;
  }
  