/* Container with full height split layout */
.start-campaign-container {
  display: flex;
  min-height: 100vh;
  background: linear-gradient(to right, #e8f5e9, #f1f8e9); /* Green-ish background */
  font-family: 'Segoe UI', sans-serif;
}

/* Left welcome panel */
.left-panel {
  flex: 1;
  background-color: #d0f0c0;
  padding: 80px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: sticky;
  top: 0;
  height: 100vh;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
}

.left-panel h1 {
  font-size: 2.4rem;
  color: #2e7d32; /* Dark green */
  margin-bottom: 20px;
}

.left-panel p {
  font-size: 1.1rem;
  color: #33691e;
  max-width: 400px;
}

/* Right form panel */
.right-panel {
  flex: 1;
  background-color: #ffffff;
  padding: 60px 50px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: -1px 0 10px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  margin: 20px;
}

/* Form title */
.form-heading {
  font-size: 1.8rem;
  color: #2e7d32;
  margin-bottom: 30px;
}

/* Form styling */
.campaign-form {
  display: flex;
  flex-direction: column;
}

.campaign-form label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

.campaign-form input,
.campaign-form textarea,
.campaign-form select {
  padding: 10px 14px;
  margin-bottom: 20px;
  border: 1px solid #b2dfdb;
  border-radius: 6px;
  font-size: 1rem;
  background: #fdfdfd;
  transition: border 0.2s;
}

.campaign-form input:focus,
.campaign-form textarea:focus {
  border-color: #43a047;
  outline: none;
}

/* Category buttons */
.category-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 25px;
}

.category-buttons button {
  padding: 8px 16px;
  border: 1px solid #ccc;
  border-radius: 20px;
  background: #f5f5f5;
  cursor: pointer;
  transition: 0.2s ease;
  font-size: 0.95rem;
  color: #333;
}

.category-buttons button:hover {
  background-color: #c8e6c9;
  border-color: #66bb6a;
}

.category-buttons .selected {
  background-color: #2e7d32;
  color: white;
  border-color: #2e7d32;
}

/* Submit button */
.submit-btn {
  background-color: #2e7d32;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease;
  margin-top: 10px;
}

.submit-btn:hover {
  background-color: #1b5e20;
}

/* Mobile responsive */
@media (max-width: 900px) {
  .start-campaign-container {
    flex-direction: column;
  }

  .left-panel,
  .right-panel {
    flex: none;
    width: 100%;
    padding: 40px 30px;
    border-radius: 0;
  }

  .left-panel {
    text-align: center;
    border-radius: 0;
  }
}
